import React from "react";
import ProjectCard from "./ProjectCard.js";
import "./ProjectCard.css";

import texteditorImage from "../img/texteditor.PNG";
import unixShell from "../img/unixShell.PNG";
import spaceAttackImage from "../img/spaceAttackImage.PNG";
import mortgageCalculatorImage from "../img/MortCalc.jpg";

function Projects() {
  return (
    <div>
      <h1 className="section-heading">Projects</h1>
      <hr className="projectHeading-hr"></hr>

      <div id="projects" className="container">
        <div className="subContainer">
          <ProjectCard
            src={texteditorImage}
            name="BRIM Text Editor"
            github_src="https://github.com/manasb12/BRIM-Text-Editor"
            date="Mar 2021"
            description="A Text Editor made in Python using the TKinter module. The
                    motivation behind this editor is simplicity while providing various themes, 
                    color choices and other useful features. Includes the ability to search and replace
                    a word. Implemented menu features (save, open, edit) files."
            tech="Python, TKinter"
          />
          <ProjectCard
            src={unixShell}
            name="UNIX Shell"
            date="Oct 2020 - Nov 2020"
            description="Designed a C program to serve as a shell interface that accepts user commands
            and executes each command in a separate process. Implemented I/O redirection and
            IPC between a pair of commands. Used fork(), exec(), wait(), dup2(), and pipe()
            system calls. Received a grade of A+."
            tech="C"
          />
        </div>
        <div className="subContainer">
          <ProjectCard
            src={mortgageCalculatorImage}
            name="Mortgage Calculator"
            github_src="https://github.com/manasb12/Mortgage-Calculator"
            date="July 2019"
            description="A Mortgage Calculator App that calculates monthly mortgage payments 
                    based on principal amount, amoritization and interest. Text to Speech function reads
                    out the leftover payments after each year."
            tech="Android Studio, Java"
          />
          <ProjectCard
            src={spaceAttackImage}
            name="Space Attack"
            github_src="https://github.com/manasb12/Space-Attack"
            date="June 2018"
            description="A 2D top-down shooter made in Java using Graphics2D library. Added a Menu System, 
            multiple enemies, powerups and an infinte scrolling background. Received a grade of A+."
            tech="Java, Graphics2D, Paint.NET"
          />
        </div>
      </div>
    </div>
  );
}

export default Projects;
