import React from "react";
import "./JobComponent.css";

function Experience() {
  return (
    <div>
      <h1 id="headerTitle">Experience</h1>
      <div id="experience" className="experience-main">
        <hr className="headings-hr"></hr>
        <div className="experienceContainer">
          <SubComponent
            src="https://pbs.twimg.com/profile_images/1258154808134922242/sUXk1gdX_400x400.jpg"
            jobTitle="Senior Technical Student"
            company="Toronto Hydro"
            date="May 2021 - Dec 2021 | Toronto, ON"
          />
          <ul>
            <li>
              Currently working in the <span>IT Portfolio Management</span> dept at Toronto Hydro.
            </li>
          </ul>
        </div>

        <hr className="headings-hr"></hr>
        <div className="experienceContainer">
          <SubComponent
            src="https://pbs.twimg.com/profile_images/1278074645288292358/_Wwp4gQy_400x400.jpg"
            jobTitle="Smart Systems Intern"
            company="Mackenzie Health"
            date="Jan 2021 - Apr 2021 | Vaughan, ON"
          />
          <ul>
            <li>
              Worked in the <span>ICAT department</span> at the{" "}
              <span>Cortellucci Vaughan Hospital</span>; Canada's first Smart
              Hospital.
            </li>
            <li>
              Validated smart devices ensuring they are on, connected and
              displaying correct information.
            </li>
            <li>
              Performed extensive <span>MRT Testing</span> and organized
              employee <span>database spreadsheets</span>.
            </li>
            <li>
              <span>Continually collborated</span> with the Application
              Integration and IT team to ensure successful opening of the
              Hospital.
            </li>
          </ul>
        </div>
        <hr className="headings-hr"></hr>
        <div className="experienceContainer">
          <SubComponent
            src="https://media.glassdoor.com/sqll/141035/york-university-squarelogo.png"
            jobTitle="Peer Tutor"
            company="Lassonde School of Engineering"
            date="Feb 2019 - Apr 2019 | Toronto, ON"
          />
          <ul>
            <li>
              Selected by the Professor in{" "}
              <span>Introduction to Web Development</span> course to help other
              students in the class.
            </li>
            <li>
              <span>Helped</span> students with{" "}
              <span>HTML, CSS, JavaScript, and Flowcharts</span> in scheduled
              meetings.
            </li>
          </ul>
        </div>
      </div>
      <hr className="hr-fullSize"></hr>
    </div>
  );
}

function SubComponent(props) {
  return (
    <div>
      <img src={props.src} />
      <h1 className="jobTitle">{props.jobTitle}</h1>
      <h3 className="company">{props.company}</h3>
      <h3 className="date">{props.date}</h3>
    </div>
  );
}

export default Experience;
